.Menu {
  position: absolute;
  right: 20px;
  top: 60px;
  width: 200px;
  text-align: right;
  background-color: #fff;
  box-shadow: inset hoff voff blur #000;
  color: #00838F;
  box-shadow: 0 0 5px 1px #00838F;
  z-index: 20;
  li {
    a {
      display: block;
      padding: 10px;
      text-transform: capitalize;
      &:hover {
        background-color: #E0F7FA;
      }
    }
  }
}

.Profile {
  p {
    padding: 5px 10px;
    &.active,
    &:hover {
      background-color: #006064;
      cursor: pointer;
    }
  }
}
