.IsOwner {
  text-align: right;
  .Body {
    justify-content: flex-end;
  }
}

.Message {
  display: block;
  padding: 0 20px;
  &:last-child {
    padding-bottom: 10px;
  }
}

.Body {
  display: flex;
  align-items: flex-start;
  padding: 2px 0;
}

.Status {
  background-color: #00838F;
  display: inline-block;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  margin-right: 10px;
  margin-top: 17px;
}

.Sending {
  background-color: #E91E63;
}

.NotReadBlock {
  background-color: #F0F4C3;
}

.Content {
  background-color: #E0F7FA;
  padding: 10px;
  border-radius: 4px;
  white-space: pre-wrap;
  word-wrap: break-word;
  max-width: 400px;
}

.Owner {
  margin-bottom: 5px;
  padding-top: 5px;
  margin-top: 5px;
  p {
    font-weight: 700;
    color: #006064; 
  }
}

.Time {
  width: 60px;
  font-size: 12px;
  text-align: right;
  margin-top: 14px;
}