.Container {
  padding: 20px;
}

.Input {
  outline: none;
  padding: 6px 0;
  border: 0;
  resize: none;
  border-bottom: 2px solid rgba(0,0,0,.2);
  box-sizing: border-box;
  flex-grow: 1;
  margin-right: 15px;
  overflow: hidden;
  &:focus {
    border-color: #00B8D4;
  }
}

.InputWrapper {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
}

.Placeholder {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: -1;
  opacity: 0.7;
  font-size: 14px;
}