.Item {
  cursor: pointer;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 50px;
  &:hover {
    background-color: #e1f5fe;
  }
  &.Active {
    background-color: #0097a7;
    color: #fff;
  }
}

.NotRead {
  background-color: #1DE9B6;
  color: #000;
  height: 20px;
  line-height: 20px;
  min-width: 20px;
  text-align: center;
  border-radius: 10px;
  font-size: 13px;
  padding: 0 5px;
  box-sizing: border-box;
}
